<template>
  <div class='container'>
    <div ref='outline' class='outline'>
      <div style='border: 1px solid #eee; text-align: center; padding: 10px;'>大綱</div>
      <div>
      <el-tree :props="defaultProps" :data="outline" @node-click="onNodeClick"
      ></el-tree>
      </div>
    </div>
    <div class="ckeditor" ref="ckeditor">
      <textarea :name="name" :id="id" :value="value" :types="types" :config="config">
      </textarea>
    </div>
  </div>
</template>

<script>
let inc = new Date().getTime()
export default {
  name: 'vue-ckeditor',
  props: {
    name: {
      type: String,
      default: () => `editor-${++inc}`
    },
    value: {
      type: String
    },
    id: {
      type: String,
      default: () => `editor-${inc}`
    },
    types: {
      type: String,
      default: () => `classic`
    },
    config: {
      type: Object,
      default: () => {}
    }
  },
  data () {
    return { 
      destroyed: false,
      outline: [{
        label: "第一章",
        children: [{
          label: "第一節"
        }, {
          label: "第二節"
        }]
      }],
      defaultProps: {
        children: 'children',
        label: 'label'
      }
    }
  },
  computed: {
    instance () {
      return CKEDITOR.instances[this.id]
    }
  },
  watch: {
    value (val) {
      try {
        if (this.instance) {
          this.update(val)
        }
      } catch (e) {}
    }
  },
  mounted () {
    this.create()
  },
  beforeDestroy () {
    this.destroy()
  },
  methods: {
    onNodeClick(data){
      console.log(data)
    },
    create () {
      console.clear();
      let self = this;
      let myConfig = {
        toolbar: [
          [ 'Source', '-', 'Save', 'NewPage', 'Preview', 'Print', '-', 'Templates' ],
          [ 'Cut', 'Copy', 'Paste', 'PasteText', 'PasteFromWord', '-', 'Undo', 'Redo' ],
          [ 'Find', 'Replace', '-', 'SelectAll', '-', 'Scayt' ],
          //[ 'Form', 'Checkbox', 'Radio', 'TextField', 'Textarea', 'Select', 'Button', 'ImageButton', 'HiddenField' ],
          [ 'Bold', 'Italic', 'Underline', 'Strike', 'Subscript', 'Superscript', '-', 'CopyFormatting', 'RemoveFormat' ],
          [ 'NumberedList', 'BulletedList', '-', 'Outdent', 'Indent', 
            '-', 'Blockquote', 'CreateDiv', 
            '-', 'JustifyLeft', 'JustifyCenter', 'JustifyRight', 'JustifyBlock', 
            //'-', 'BidiLtr', 'BidiRtl', 'Language' 
          ],
          [ 'Link', 'Unlink', 'Anchor' ],
          [ 'Image', 'Flash', 'Youtube', 'Table', 'HorizontalRule', 'Smiley', 'SpecialChar', 'PageBreak', 'Iframe' ] ,
          [ 'Styles', 'Format', 'Font', 'FontSize' ],
          [ 'TextColor', 'BGColor' ] ,
          [ 'Maximize', 'ShowBlocks' ] ,
          [ 'About' ]
        ],
        //width: "80%",
        //height: 200,
        //removePlugins: 'elementspath',
        //fontSize_sizes: '16/16px;24/24px;48/48px;'
      }
      //this.config.fontSize_sizes = '16/16px;24/24px;48/48px;'
      for(let key in myConfig){
        if(typeof this.config[key] == "undefined"){
          this.config[key] = myConfig[key];
        } 
      }
      //this.config.removePlugins = 'elementspath';
      if (typeof CKEDITOR === 'undefined') {
        console.log('CKEDITOR is missing (http://ckeditor.com/)')
      } else {
        if (this.types === 'inline') {
            CKEDITOR.inline(this.id, this.config)
        } else {
            CKEDITOR.replace(this.id, this.config)
        }

        this.instance.setData(this.value)
        this.instance.on('instanceReady', (evt) => {
          this.instance.setData(this.value);
          this.resize();
        })
        this.instance.on('change', this.onChange)
        this.instance.on('blur', this.onBlur)
        this.instance.on('focus', this.onFocus);

        this.instance.on('afterCommandExec', function(event){ // ok.....
          // ok-------------------
          let commandName = event.data.name;
          console.log("commandName: " + commandName)

          // ok-------------------
          let sel = self.instance.getSelection();
          let element = sel.getStartElement();
          sel.selectElement(element);
          console.log(sel)
          console.log(element)
          
        });

        window.onresize = () => {
          return (() => {
              self.resize();
          })()
        }
      }
    },
    resize(){ // ok
      let editorElem = this.$refs["ckeditor"];
      this.instance.resize("100%", editorElem.clientHeight);
    },
    update (val) {
      let html = this.instance.getData()
      if (html !== val) {
        this.instance.setData(val, { internal: false })
      }
    },
    destroy () {
      if (!this.destroyed) {
        this.instance.focusManager.blur(true)
        this.instance.removeAllListeners()
        this.instance.destroy()
        this.destroyed = true
      }
    },
    onChange () {
      let html = this.instance.getData()
      if (html !== this.value) {
        this.$emit('input', html)
      }
    },
    onBlur () {
      this.$emit('blur', this.instance)
    },
    onFocus () {
      this.$emit('focus', this.instance)
    }
  }
}
</script>
<style>
.container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;
  overflow: hidden;
}
.outline {
  width: 200px;
  height: 100%;
  overflow: hidden;
}
.ckeditor {
  height: 100%;
  flex: 1;
  overflow: hidden;
}
.ckeditor::after {
  content: "";
  display: table;
  clear: both;
}
</style>



