import Vue from 'vue'
import Router from 'vue-router'
import Home from '../main/home'
import List from '../main/list'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    }, {
      path: '/list',
      name: 'list',
      component: List
    }
  ]
})



