<template>
	<div class='container'>
    <div ref='section' class='section'>
      <div style='border: 1px solid #eee; text-align: center; padding: 10px; width: 100%;'>大綱</div>
      <div class='tree'>
      	<el-tree :props="defaultProps" :data="section" @node-click="onNodeClick"></el-tree>
      </div>
    </div>
		<div class="editor" ref="editor">
  		<script :id="id" type="text/plain"></script>
		</div>
	</div>
</template>

<script>
export default {
	name: 'UEditor',
	data() {
		return {
			id: 'editor' + Math.random().toString().slice(-10),
			editor: null,
			defaultConfig: {
				UEDITOR_HOME_URL: '../static/UEditor/',
				autoHeightEnabled: false,
        initialFrameHeight: 600, //'100%',
				scaleEnabled: true,
        initialFrameWidth: '100%',
				zIndex: 9999
			},
			section: [],
      defaultProps: {
        children: 'children',
        label: 'label'
      }
		}
	},
	props: {
		value: {
			type: String,
			default: ''
		},
		config: {
			type: Object,
			default: function() {
				return {}
			}
		},
	},
	created(){
		console.clear();
	},
	mounted(){
		//console.log("id: " + this.id)
	},
	beforeDestroy () {
    this.editor.destroy()
  },
	computed: {
		mixedConfig() {
			return Object.assign({}, this.defaultConfig, this.config)
		}
	},
	methods: {
		test(){ // 沒用
			//console.log(this.editor.selection.getRange())
		},
		onNodeClick(data){
			let self = this;
			this.editor.focus();
			this.editor.execCommand('selectsection', data.address, true);
			setTimeout(function(){
    		let node = self.editor.selection.getStart();
				node.scrollIntoView();
			}, 100)
		},
		updateSections(){
			let dir = this.editor.execCommand('getsections');
			this.section = traversal(dir);
			function traversal(section) {
					//var $list, $item, $itemContent, 
					let list = [];
					if(section.children.length) {
							//$list = $('<ul>');
							for(var i = 0; i< section.children.length; i++) {
									let child = section.children[i];
									let obj = {
										label: getSubStr(child['title'], 18),
										address: child
									};
									if(child.children.length){
										obj.children = traversal(child);
									}

									list.push(obj);
							}
					}
					return list;
			}
			function getSubStr(s,l){
        var i=0,len=0;
        for(i;i<s.length;i++){
            if(s.charAt(i).match(/[^\x00-\xff]/g)!=null){
                len+=2;
            }else{
                len++;
            }
            if(len>l){ break; }
        }return s.substr(0,i);
    	};
		},
		resize(){
			let self = this;
			let editorElem = this.$refs["editor"];
			let tool = document.querySelector("#" + this.id + " .edui-editor-toolbarbox");
			let bottom = document.querySelector("#" + this.id + " .edui-editor-bottomContainer");

			self.editor.setHeight(editorElem.clientHeight - 
				tool.clientHeight - bottom.clientHeight - 5
			);
		},
		// 实例化编辑器之前-JS依赖检测
		_beforeInitEditor(value) {
			// 准确判断ueditor.config.js和ueditor.all.js均已加载 仅加载完ueditor.config.js时UE对象和UEDITOR_CONFIG对象也存在,仅加载完ueditor.all.js时UEDITOR_CONFIG对象也存在,但为空对象
			!!window.UE && !!window.UEDITOR_CONFIG && Object.keys(window.UEDITOR_CONFIG).length !== 0 && !!window.UE.getEditor ? this._initEditor(value) : this._loadScripts().then(() => this._initEditor(value))
		},
		// 实例化编辑器
		_initEditor(value) {
			let self = this;
			this.$nextTick(() => {
				// 没有按官网示例那样链式调用ready方法的原因在于需要拿到getEditor返回的实例
				this.editor = UE.getEditor(this.id, this.mixedConfig)
				this.editor.addListener('updateSections', this.updateSections);
				this.editor.addListener("ready", () => {
					this.$emit('ready', this.editor)
					if(typeof localStorage["ueditor"] == "string") {
						this.editor.setContent(localStorage["ueditor"])
					} else 
						this.editor.setContent(value)
					this.editor.addListener("contentChange", () => {
						localStorage["ueditor"] = this.editor.getContent(); // .................. test
						this.$emit('input', this.editor.getContent())
					});
					self.test();

					window.onresize = () => {
						return (() => {
								self.resize();
						})()
					}
					self.resize();
				});
			})
		},
		// 动态添加JS依赖
		_loadScripts() {
			// 确保多个实例同时渲染时不会重复创建SCRIPT标签
			if (window.loadEnv) {
				return new Promise((reslove, reject) => {
					window.addEventListener('loadEnv', function() {
						reslove()
					});
				})
			} else {
				window.loadEnv = new Event('loadEnv')
				return new Promise((reslove, reject) => {
					// 如果在其他地方只引用ueditor.all.min.js，在加载ueditor.config.js之后仍需要重新加载ueditor.all.min.js，所以必须确保ueditor.config.js已加载
					this._loadConfig().then(() => this._loadCore()).then(() => {
						window.dispatchEvent(window.loadEnv);
						reslove()
					})
				})
			}
		},
		_loadConfig() {
			return new Promise((reslove, reject) => {
				if (!!window.UE && !!window.UEDITOR_CONFIG && Object.keys(window.UEDITOR_CONFIG).length !== 0) {
					reslove()
					return
				}
				let configScript = document.createElement('script')
				configScript.type = 'text/javascript'
				configScript.src = this.mixedConfig.UEDITOR_HOME_URL + 'ueditor.config.js'
				document.getElementsByTagName('head')[0].appendChild(configScript)
				configScript.onload = function() {
					if (!!window.UE && !!window.UEDITOR_CONFIG && Object.keys(window.UEDITOR_CONFIG).length !== 0) {
						reslove()
					} else {
						console && console.error('加载ueditor.config.js失败,请检查您的配置地址UEDITOR_HOME_URL填写是否正确!')
					}
				};
			})
		},
		_loadCore() {
			return new Promise((reslove, reject) => {
				if (!!window.UE && !!window.UE.getEditor) {
					reslove()
					return
				}
				let coreScript = document.createElement('script')
				coreScript.type = 'text/javascript'
				coreScript.src = this.mixedConfig.UEDITOR_HOME_URL + 'ueditor.all.min.js' // 
				document.getElementsByTagName('head')[0].appendChild(coreScript)
				coreScript.onload = function() {
					if (!!window.UE && !!window.UE.getEditor) {
						reslove()
					} else {
						console && console.error('加载ueditor.all.min.js失败,请检查您的配置地址UEDITOR_HOME_URL填写是否正确!')
					}
				};
			})
		},
		// 设置内容
		_setContent(value) {
			value === this.editor.getContent() || this.editor.setContent(value)
		},
	},
	// v-model语法糖实现
	watch: {
		value: {
			handler(value) {
				!!this.editor ? this._setContent(value) : this._beforeInitEditor(value)
			},
			immediate: true
		}
	}
}
</script>
<style>
.container {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;
  overflow: hidden;
}
.section {
  width: 200px;
  height: 100%;
  overflow: hidden;
	display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
.tree {
	flex: 1;
	overflow-y: auto;
	width: 100%;
}
.editor {
  height: 100%;
  flex: 1;
  overflow: hidden;
}
.editor::after {
  content: "";
  display: table;
  clear: both;
}
</style>


